import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import styled from 'styled-components';
import SEO from '@common/SEO';

import Fade from 'react-reveal/Fade';
import Layout from '@common/Layout';
import Footer from '@common/Footer';

import EmailForm from '@ui/emailform';
import {Container, Section} from '@components/global';
import SectionHeader from '@ui/sectionheader';

import branding from '@static/og/branding.jpg';



const Subsciption = () => (
  <StaticQuery
    query={graphql`
      query {
        Media: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "branding" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },   
        Photos: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "test0" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },   
      }
    `}
    render={data => (
      <Layout>
        <SEO title="Subscibe Us | Rearia - Hearing Technology for Everyone" desc="Stay tuned about our progress and get a secret discount!" img={branding} url="https://rearia.io/subsciption/"/>
        <Section id="subscription">
          <SectionHeader 
            header="Hai: World's First Bluetooth Earphones for All"
            subheader="The first modular earphone that's universal for both hard-of-hearing and hearing people." 
          />

          <Container>
            <Grid>
              <div>
                <Fade>
                  <h3>Stay tuned about our progress and get a secret discount!</h3>
                  <h4>We need your help to complete Hai</h4>
                  <p>Please sign up to our newsletter and receive an exclusive article <i><b>How do hard of hearing people listen to music</b></i>. Follow our social media to get the latest information on Hai. We'll also share tips and tricks about music and hearing aids.</p> 
                  <p>If you know someone who'd appreciate having our product, please let them know of Hai too! <b>We need your help to realize Hai for everyone!</b></p>
                  <EmailForm accent="inverse"/>
                </Fade>
              </div>
              <div/>
            </Grid>
            <Grid>
              <div>
                <Fade>
                  <h3>Wanna know more about Hai?</h3>
                  <p>Please sign up to our newsletter and receive an exclusive article <i><b>How do hard of hearing people listen to music</b></i>. Follow our social media to get the latest information on Hai. We'll also share tips and tricks about music and hearing aids.</p> 
                  <p>If you know someone who'd appreciate having our product, please let them know of Hai too! <b>We need your help to realize Hai for everyone!</b></p>
                </Fade>
              </div>
              <div/>
            </Grid>
          </Container>
        </Section>

        <Footer />
      </Layout>
    )}
  />
);


const Grid = styled.div`
  display: grid;
  grid-template-columns: 2fr 2fr 1fr;
  align-items: top;
  grid-gap: 64px;
  margin-top: 30px;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    grid-gap: 80px;
    margin-top: 0;
  }
  
`;

export default Subsciption;
